//RobotoCondensed

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed/robotocondensed-regular.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed/robotocondensed-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed/robotocondensed-bold.woff2") format("woff2");
  src: url("../fonts/RobotoCondensed/robotocondensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

